*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    border: 1px solid red;  
}




.card-container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    border-radius: 1rem;
    background: rgba(111, 111, 111, 0.2);
    width: 100%;
    height: 15rem;
}

.card-container h2{
    font-size: 1rem;
}

.weather-title{
    text-align: left;
    font-style: italic;
    font-weight: 100;
}

.card-footer{
    display: flex;
    align-items: end;
    gap: .50rem;
}

.weather-value{
    text-align: right;
    font-weight: 150;
    font-size: 200%
}


.caption{
    font-size: clamp( 1px, 1.4vw, .75rem);
    text-align: left;
    font-weight: 125;
    padding-bottom: .75rem;
}