body, header, footer, .card-header{
    width: 100%;
    border: 1px solid red;
}



html {
    overflow: hidden;
}

.app{
    /* Structure */
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;

    /* Aesthetic */
    padding: 2rem;
    height: 100vh;
    width: 100vw;
    overflow: hidden;
    text-align: center;
    gap: 2.5rem;


    /* Color-Styling */
    background-color: black;
    color: white;
}




.app > body {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: auto;
    gap: 1rem;
    height: 100%;
    width: 100%;
    place-content: center;
    overflow: hidden;
}




@media (max-width: 900px) {
    .app > body {
        display: grid;
        grid-template-columns: repeat(2, 2fr);
        grid-template-rows: auto;
        gap: 1rem;
        height: 100%;
    }

    
}




input{
    padding: .2rem .8rem ;
    border-radius: .75rem;
    width: 75%;
}